    family: '\u6211\u5F88\u5E78\u904B, \u6709\u4E00\u500B\u5065\u5EB7\u7684\u5BB6\u5EAD. \u5BB6\u88E1\u9084\u6709\u4E00\u72D7\u4E00\u8C93. \u5F9E\u6211\u51FA\u8EAB\u5230\u73FE\u5728, \u6211\u7684\u8EAB\u65C1\u90FD\u6709\u8457\u72D7\u7684\u966A\u4F34, \u4ED6\u5011\u5C31\u50CF\u6211\u7684\u5BB6\u4EBA\u4E00\u6A23, \u8DDF\u8457\u6211\u5011\u4E00\u8D77\u5403, \u4E00\u8D77\u73A9, \u4E00\u8D77\u7761. \u6211\u5011\u6BCF\u5929\u90FD\u6703\u51FA\u9580\u6E9C\u8C93\u8DDF\u72D7, \u5E36\u4ED6\u5011\u6563\u6B65! \u4E0D\u5E36\u4ED6\u5011\u51FA\u53BB\u9084\u6703\u54ED\u5462! \u6211\u7684\u7236\u6BCD\u975E\u5E38\u958B\u660E, \u4ED6\u5011\u4E00\u5411\u90FD\u5F88\u5C0A\u91CD\u6211\u7684\u60F3\u6CD5\u4E26\u4E14\u652F\u6301\u8457\u6211, \u9019\u771F\u7684\u662F\u5F88\u96E3\u80FD\u53EF\u8CB4\u7684\u4E00\u4EF6\u4E8B! \u6211\u611B\u6211\u7684\u5BB6\u4EBA, \u80FD\u64C1\u6709\u9019\u6A23\u4E00\u500B\u5B8C\u6574\u7684\u5BB6\u5EAD, \u771F\u7684\u5F88\u5E78\u904B!'
